
$margin:20px;
.card {
    height: auto;
    width: 350px;
    
    background-color: white;

    border-radius: $margin;

    img {
        width: 310px;
        margin: $margin;
        border-radius: 25px;
    }

    h1 {
        font-size: 24px;;
        margin: 0 $margin;
    }

    p {
        margin: $margin;
    }
}